.preloader_type_text {
    max-width: 100%;
    height: 22px;
    margin: 0 6px 8px 0;
    background-color: #333;
    position: relative;
    overflow: hidden;
}

.preloader_type_text::after {
    content: '';
    width: 30px;
    height: 11px;
    background-color: rgba(204, 204, 204, .30);
    border-color: #ccc;
    box-shadow: 0 0 20px 0;
    position: absolute;
    top: 5px;
    left: 5px;
    animation: loading ease-in-out 1s infinite;
}

@media screen and (min-width: 600px) {
    .ader_type_text {
        margin: 0 14px 10px 0;
    }
}

@keyframes loading {
    from {
        left: -35px;
    }

    to {
        left: 100%;
    }
}